import Card from './card'
import ButtonAdd from './elements/buttonAdd'
import TextBox from './elements/textBox'
export default function CardList(props, state) {
  state = {
    cards: []
  }

  const addCard = () => {
    state.cards = state.cards.concat(["abc"],state.cards)
    console.log('addCard',state.cards)

  }
  return (
    <div>
      <p>{props.title}</p>
      {
        state.cards.map((card) => {
          <Card content={card}/>
        })
      }
      <ButtonAdd onClick={addCard}/>
      <TextBox/>
    </div>
  )
}